body {
  background: #eee !important;
}
.card {
  border: 0.4rem solid #f8f9fa;
  top: 10%;
}
.form-control {
  background-color: #f8f9fa;
  padding: 20px;
  padding: 25px 15px;
  margin-bottom: 1.3rem;
}

.form-control:focus {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #007bffa1;
  outline: 0;
  box-shadow: none;
}

.btn {
  padding: 0.6rem 1.2rem;
  background: #da5767;
  border: 2px solid #da5767;
}
.btn-primary:hover {
  background-color: #df8c96;
  border-color: #df8c96;
  transition: 0.3s;
}
.activeButton {
  color: #fff;
  background-color: #545b62;
  border-color: #4e555b;
}
.inputField.error::before {
  content: "x";
  display: inline-block;
  margin-top: 10px;
  font-weight: 600;
  color: #ca3938;
}
.button-search {
  width: 50%;
  position: relative;
  left: 25%;
}
.searchBar {
  border-radius: 50px;
}
.has-search {
  width: 50%;
  position: relative;
  left: 50%;
  top: 10px;
  transform: translate(-50%, 0%);
}
.has-search .form-control {
  padding-left: 2.375rem;
}
.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}
.imgList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: 10px;
}
.imgList img {
  width: 250px;
}
.noData {
  text-align: center;
  font-size: 24px;
}
.logout {
  margin: 1em;
}
